import http from './http';

export const LoginApi = (model) => http.post('/public/login', model);
export const RegisterApi = (model) => http.post('/public/register', model);
export const CaptchaApi = () => http.get('/../common/captcha');

export const DashBoardApi = () => http.get('/info/dashboard');

export const UserListApi = () => http.get('/user');
export const UserBanApi = (id) => http.delete('/user/' + id);

export const WalletListApi = (id) => http.get('/wallet/' + id);
export const WalletEditApi = (model) => http.post('/wallet', model);

export const BankApi = (id) => http.get('/bank/' + id);
export const BankEditApi = (model) => http.post('/bank', model);

export const AdminApi = () => http.get('/user/admin');
export const AdminSaveApi = (model) => http.post('/user/admin', model);
export const AdminDelApi = (id) => http.delete('/user/admin/' + id);

export const RostateApi = () => http.get('/rotate');
export const RostateSaveApi = (model) => http.post('/rotate', model);
export const RostateDelApi = (id) => http.delete('/rotate/' + id);

export const PostApi = () => http.get('/post');
export const PostSaveApi = (model) => http.post('/post', model);
export const PostDelApi = (id) => http.delete('/post/' + id);

export const ProductApi = () => http.get('/product');
export const ProductSaveApi = (model) => http.post('/product', model);
export const ProductDelApi = (id) => http.delete('/product/' + id);

export const TransactionApi = () => http.get('/transaction');

export const FinancialApi = () => http.get('/finance');
export const RechageApi = () => http.get('/recharge');
export const WithdrawListApi = () => http.get('/withdraw');
export const WithdrawAllowApi = (model) => http.post('/withdraw', model);
export const WithdrawAllowHandlApi = (model) => http.put('/withdraw', model);
export const WithdrawDelApi = (id) => http.delete('/withdraw/' + id);

export const SetApi = (type) => http.get('/set/' + type);
export const SetSaveApi = (model) => http.post('/set', model);
